<template>
  <div>
    <Layout-box one="主播认证">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="realName" label="姓名" />
        <el-table-column prop="frontView" label="身份证正面">
          <template slot-scope="scope">
            <el-image :src="scope.row.frontView" fit="contain" />
          </template>
        </el-table-column>
        <el-table-column label="身份证背面">
          <template slot-scope="scope">
            <el-image :src="scope.row.backView" fit="contain" />
          </template>
        </el-table-column>
        <el-table-column label="手持身份证照片">
          <template slot-scope="scope">
            <el-image :src="scope.row.handsetView" fit="contain" />
          </template>
        </el-table-column>
        <el-table-column prop="cerNo" label="身份证号码" width="200px" />
        <el-table-column label="申请时间" width="140px">
          <template slot-scope="scope">
            {{ _formatDate(scope.row.createTime) }}
          </template>
        </el-table-column>
        <el-table-column prop="mobile" label="手机" width="120px" />
        <el-table-column label="状态" width="70px">
          <template slot-scope="scope">
            <p v-if="scope.row.status === 2">拒绝</p>
            <p v-else-if="scope.row.status === 1">通过</p>
            <p v-else>待审核</p>
          </template>
        </el-table-column>
        <el-table-column prop="reason" label="原因" show-overflow-tooltip />
        <el-table-column label="操作" width="50px" fixed="right">
          <template slot-scope="scope">
            <el-button v-if="scope.row.status === 0" type="text" @click="onAudit(scope.row)">审核</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInit.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </Layout-box>

    <el-dialog title="审核" :visible.sync="dialogFormVisible">
      <span>原因：</span>
      <el-input v-model="reason"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="onSubmit(2)">拒绝</el-button>
        <el-button type="primary" @click="onSubmit(1)">同意</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import { formatDate } from '@/utils/date.js'

export default {
  name: 'liveList',

  components: {
    LayoutBox,
  },

  data () {
    return {
      tableData: [],
      queryInit: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },
      reason: '', // 原因
      selectData: '', // 选择的用户

      // --------------------------------
      dialogFormVisible: false, // 审核窗口
    }
  },

  async created () {
    await this.apiQueryAnchor()
  },

  methods: {
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
    },
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
    },
    // 点击审核
    onAudit (row) {
      this.selectData = row
      this.dialogFormVisible = true
    },
    // 点击确定
    async onSubmit (status) {
      const data = {
        ...this.selectData,
        reason: this.reason,
        status: status,
      }
      const res = await this.$http.post(this.$api.certificationAnchor, data)
      if (res.data.success) {
        this.$message.success('保存成功')
        this.dialogFormVisible = false
        await this.apiQueryAnchor()
      } else {
        this.$message.error(res.data.msg)
      }
    },

    // ---------------------------------------------------------------------
    // 主播注册申请列表查询
    async apiQueryAnchor () {
      const data = {
        ...this.queryInit
      }
      const res = await this.$http.post(this.$api.queryAnchor, data)
      const HTTP_LIVE = 'http://live.woho.live'
      this.tableData = res.data.data.list.map((item) => {
        item.backView = HTTP_LIVE + item.backView
        item.frontView = HTTP_LIVE + item.frontView
        item.handsetView = HTTP_LIVE + item.handsetView
        return item
      })
      console.log('this.tableData', this.tableData)
      this.queryInit = res.data.data
    },
    // 格式化时间
    _formatDate (date) {
      return formatDate(new Date(date), 'yyyy-MM-dd hh:mm')
    }
  },
}
</script>

<style lang="scss" scoped>

</style>
